template {
  display: none; }

ul,
ol,
dl {
  margin: 0;
  padding: 0; }

li {
  margin: 0;
  padding: 0;
  list-style: none; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

img {
  height: auto;
  border: 0;
  max-width: 100%;
  vertical-align: top; }

svg:not(:root) {
  overflow: hidden; }

a {
  color: #262624;
  text-decoration: underline; }
  a:active, a:hover {
    outline-width: 0;
    text-decoration: none; }

p {
  margin: 0; }

dfn {
  font-style: italic; }

abbr[title] {
  border-bottom: 1px dotted; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
